import VectorTileLayer from 'ol/layer/VectorTile';
import VectorTileSource from 'ol/source/VectorTile';
import MVTFormat from 'ol/format/MVT';
import vtstyle from '../styles/vtstyle';

const vt = new VectorTileLayer({
  source: new VectorTileSource({
    format: new MVTFormat({
      layers: ['river', 'coastline', 'lake', 'waterarea', 'road', 'railway', 'label', 'contour', 'building', 'boundary', 'searoute'],
    }),
    url:
      'https://cyberjapandata.gsi.go.jp/xyz/experimental_bvmap/{z}/{x}/{y}.pbf',
    attributions: [
      '<a href="https://github.com/gsi-cyberjapan/gsimaps-vector-experiment" target="_blank" rel=noopener noreferrer></a>',
    ],
    cacheSize: window.innerWidth < 600 ? 8 : 32
  }),
  declutter: true,
  style: vtstyle,
});

export default vt;
